#uiowa-search {
  flex-direction: column;
}

@media (min-width: 768px) {
  #uiowa-search {
    flex-direction: row;
  }
}

.form-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -5px;
  margin-left: -5px;
  margin-top: 13px;
}

fieldset.form-item-search-terms {
  position: relative;
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 0;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 66.66667%;
  flex: 0 0 66.66667%;
  max-width: 66.66667%;
}

.btn-dark,
#uiowa-bar--wrapper .uiowa-bar--navbar--search-form input.form-submit {
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
}

#uiowa-bar--wrapper .uiowa-bar--navbar--search-form input.form-submit {
  border-radius: 0 .2rem .2rem 0;
}

.form-control {
  display: block;
  width: 100%;
  height: calc(2.25rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0;
  -webkit-transition: border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
}

.form-control-sm {
  height: calc(1.8125rem + 2px);
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.2rem;
}


button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

.btn,
.node-preview-container .node-preview-backlink {
  white-space: normal !important;
  word-wrap: break-word;
}

.btn-sm,
.btn-group-sm>.btn,
.node-preview-container .btn-group-sm>.node-preview-backlink {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0;
}

.btn {
  padding: 0.375rem 0.75rem;
  border: 1px solid transparent;
  display: inline-block;
  font-weight: 400;
  color: #212529;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0;
  -webkit-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
}

.ml-0,
.mx-0 {
  margin-left: 0 !important;
}

.mr-0,
.mx-0 {
  margin-right: 0 !important;
}

.sr-only,
.block-superfish .block__title,
.block-superfish #superfish-main-toggle span {
  padding: 0;
  border: 0;
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
}

.mt-0,
.my-0 {
  margin-top: 0 !important;
}

.form-text {
  display: block;
  margin-top: 0.25rem;
}


.btn-dark,
#uiowa-bar--wrapper .uiowa-bar--navbar--search-form input.form-submit {
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
}

.mr-2,
.mx-2 {
  margin-right: 0.5rem !important;
}

.d-inline-flex {
  display: -webkit-inline-box !important;
  display: -ms-inline-flexbox !important;
  display: inline-flex !important;
}

.pl-0,
.px-0 {
  padding-left: 0 !important;
}

.pr-0,
.px-0 {
  padding-right: 0 !important;
}

.ml-1,
.mx-1 {
  margin-left: 0.25rem !important;
}

.mr-1,
.mx-1 {
  margin-right: 0.25rem !important;
}

.uiowa-bar--navbar--links {
  list-style: none;
  margin-bottom: 0.25rem;
}

@media (min-width: 768px) {
  .uiowa-bar--navbar--links {
    margin-left: 1rem;
    margin-bottom: 0;
  }
}
